div.wrapper {
  position: relative;
  background-color: white;
  top: 62px;
  z-index: 20;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  padding: 25px;
  background-color: white;
  border-bottom: 1px solid #ccc;
  .text {
    font-size: 12px;
    color: #333;
  }
  img {
    width: 100%;
    height: 200px;
    object-fit: contain;
  }
  .title {
    font-size: 16px;
    font-weight: bold;
  }
  .subTitle {
    font-size: 12px;
  }
}
